body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 300px;
  margin: 20px auto;
  padding: 10px;
  display: block;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 10px auto;
  padding: 10px 20px;
  display: block;
}

button:hover {
  background-color: #0056b3;
}

.event-container {
  background-color: #000;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  gap: 20px;
  padding: 20px;
  display: grid;
}

.event-card {
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 50px 0;
  width: 300px;
  padding: 20px;
  transition: transform .2s ease-in-out;
}

.event-image {
  box-shadow: -3px 0 3px #ded0dac2;
}

.event-card:hover .event-image {
  box-shadow: -7px 0 10px 3px #eedee9d1;
}

.event-card:hover .square {
  box-shadow: 6px -2px 7px #eb21afd1;
}

.event-card h3 {
  color: #333;
  margin: 0;
  padding: 0;
  font-size: 24px;
}

.event-card p {
  color: #666;
  margin: 5px 0;
}

.event-card:hover {
  transform: translateY(-5px);
}

.pagination-container {
  text-align: center;
  justify-content: center;
  margin: 20px 0;
  display: flex;
}

.pagination-container button {
  color: #000;
  cursor: pointer;
  background-color: #f4f4f4;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px;
  padding: 10px 15px;
}

.pagination-container button:hover {
  background-color: #ddd;
}

.modal {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  padding-top: 60px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
  width: 80%;
  margin: 5% auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: 700;
}

.close:focus, .close:hover {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.event-card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
}

.event-card button:hover {
  background-color: #0056b3;
}

.no-events-message {
  color: #fff;
  font-size: 30px;
}

.error-message, .no-events-message {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.error-message {
  color: red;
  font-size: 20px;
}
